import React from 'react';

import ErrorComponent from '../components/ErrorComponent';

class ErrorBoundary extends React.Component {
  constructor(props) {
    super(props);
    this.state = { hasError: false, errorName: '', errorMessage: '' };
  }

  static getDerivedStateFromError({ name, message }) {
    // Update state so the next render will show the fallback UI.
    return { hasError: true, errorName: name, errorMessage: message };
  }

  componentDidCatch(error, errorInfo) {
    // You can also log the error to an error reporting service
    console.trace(errorInfo.componentStack);
  }

  render() {
    const { hasError, errorName, errorMessage } = this.state;
    const { children } = this.props;

    return hasError ? (
      <ErrorComponent errorName={errorName} errorMessage={errorMessage} />
    ) : (
      children
    );
  }
}

export default ErrorBoundary;
