import React, { Suspense } from 'react';
import { Route, withRouter, Switch, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';

import AppLayout from '../../layout/AppLayout';
import ErrorBoundary from '../../layout/ErrorBoundary';

const Dashboard = React.lazy(() =>
  import(/* webpackChunkName: "viwes-dashboard" */ './dashboard')
);
const FeedBack = React.lazy(() =>
  import(/* webpackChunkName: "viwes-feedBack" */ './feedBack')
);
const Mentor = React.lazy(() =>
  import(/* webpackChunkName: "viwes-mentor" */ './mentor')
);
const Challenges = React.lazy(() =>
  import(/* webpackChunkName: "viwes-challenges" */ './challenges')
);
const BlankPage = React.lazy(() =>
  import(/* webpackChunkName: "viwes-dashboard" */ './blank-page')
);

const App = ({ match }) => (
  <AppLayout>
    <ErrorBoundary>
      <div className="dashboard-wrapper">
        <Suspense fallback={<div className="loading" />}>
          <Switch>
            <Redirect
              exact
              from={`${match.url}/`}
              to={`${match.url}/dashboard`}
            />

            <Route
              path={`${match.url}/dashboard`}
              render={(props) => <Dashboard {...props} />}
            />

            <Route
              path={`${match.url}/challenges`}
              render={(props) => <Challenges {...props} />}
            />
            <Route
              path={`${match.url}/feedBack`}
              render={(props) => <FeedBack {...props} />}
            />
            <Route
              path={`${match.url}/mentor`}
              render={(props) => <Mentor {...props} />}
            />

            <Route
              path={`${match.url}/blank-page`}
              render={(props) => <BlankPage {...props} />}
            />

            <Redirect to="/error" />
          </Switch>
        </Suspense>
      </div>
    </ErrorBoundary>
  </AppLayout>
);

const mapStateToProps = ({ menu }) => {
  const { containerClassnames } = menu;
  return { containerClassnames };
};

export default withRouter(connect(mapStateToProps, {})(App));
